<template>
  <div class="admin-list">
    <div style="margin-bottom:30px">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>

      </el-breadcrumb>

    </div>

    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="goods_name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>

      <router-link to="/admin/goods-form">
        <el-button type="success"></el-button>
      </router-link>

    </el-form>

    <el-table v-loading='load' ref="multipleTable" @selection-change="handleSelectionChange" :data="tableData" border tooltip-effect="dark" style="width: 100%">
      <el-table-column type="selection">
      </el-table-column>

      <el-table-column prop="goods_name" label="">
      </el-table-column>

      <el-table-column label="">
        <template scope="scope">
          {{ scope.row.goods_price }}
        </template>
      </el-table-column>

      <el-table-column prop="goods_typename" label="">
      </el-table-column>

      <el-table-column prop="goods_details" label="">
      </el-table-column>

      <el-table-column width="160" label="">
        <template scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.create_time }}</span>
        </template>
      </el-table-column>

      <el-table-column label="" width="300">
        <template scope="scope">
          <el-button size="small" @click="editGoods(scope.row)">
          </el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.row)">
          </el-button>
        </template>
      </el-table-column>

    </el-table>

    <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page_num"
          :page-sizes="[5, 10, 20, 30]"
          :page-size="page_size"
 layout="total, sizes, prev, pager, next, jumper" 
:total="total"> 
</el-pagination>
<!-- <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[100, 200, 300, 400]"//
      :page-size="100"  //
      layout="total, sizes, prev, pager, next, jumper"
      :total="400" //total
>
    </el-pagination> -->
      <!-- <el-pagination 
      @current-change="handleCurrentChange" 
      layout="prev, pager, next" 
      :total="500">
      </el-pagination> -->
    </div>

  </div>
</template>

<script>
export default {
  name: "list",
  data() {
    return {
      tableData: [],
      total:0,
      page_num: 1,
      page_size: 5,
      goods_name: "",

      multipleSelection: [],

      load: false // loading
    };
  },

  methods: {
    fetchList() {
      this.load = true;

      var reqParams = {
        goods_name: this.goods_name,
        page_size: this.page_size,
        page_num: this.page_num
      };

      this.func.ajaxPost(this.api.goodsList, reqParams, res => {
        this.tableData = res.data.content;
        this.total = res.data.total;
        this.load = false;
      });
    },
    handleSizeChange(val) {
      this.page_size = val;
      this.fetchList();
    },

    //
    handleCurrentChange(val) {
      this.page_num = val;
      this.fetchList();
    },

    //
    search() {
      this.fetchList();
    },
    // 
    handleDelete(row) {
      this.$confirm(
        "[ " + row.goods_name + " ], ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          this.func.ajaxPost(
            this.api.goodsDelete,
            {
              goods_id: row.goods_id
            },
            res => {
              let index = this.tableData.indexOf(row);
              this.tableData.splice(index, 1);
            }
          );
        })
        .catch(() => {
          _this.$message({
            type: "info",
            message: ""
          });
        });
    },

    // 
    editGoods(row) {
      this.$router.push({
        path: "/admin/goods-form",
        query: {
          goods_id: row.goods_id
        }
      });
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  },

  created() {
    this.fetchList();
  }
};
</script>



